import { Navigate, useParams } from "react-router-dom"
import Navbar from "../../components/Navbar/Navbar";
import { useQuery, gql } from '@apollo/client';
import styles from './Movie.module.css'
import { CiTimer, CiHeart, CiCircleInfo } from 'react-icons/ci'
import { StoreContext } from "../../store/storeContentex";
import { useContext } from "react";


const Movie = () => {
    const params = useParams();
    const { id } = params;

    const {
        state: { user }
    } = useContext(StoreContext);
    
    const truncateTitle = (text) => {
        if(text.length > 10){
            return text.substring(0, 7) + "..."
        } return text
    }

   
    const Movie = gql`
        query {
            movie(id: ${id}) {
                info {
                    title
                    original_title
                    overview
                    vote_average
                    original_language
                    genres {
                        id
                        name
                    }
                    poster_path
                    runtime
                    vote_average
                }
   
            }
        }
    `
    const {data, loading, error} = useQuery(Movie);

    if(!user) {
        return <Navigate to="/login" />
    }

    if (!id) {
        return <Navigate to="/" />
    }

    if (loading) return "Loading...";
    if (error) return <pre>{error.message}</pre>

    const {info} = data.movie
    console.log(info)
    return (
        <>
            <Navbar />
            <div className={styles.wrapper}>
                <div className={styles.backdrop} style={{
                    backgroundImage: `url(https://image.tmdb.org/t/p/original${info.backdrop_path ? info.backdrop_path : info.poster_path})`
                }}></div>
                <div className={styles.wrapperInfo}>
                    <h1 className={styles.infoTitle}>{info.title}</h1>
                    <h3 className={styles.infoOriginal}>{info.original_title}</h3>
                    <div className={styles.otherInfo}>
                        {info.genres?.map(genre => {
                            return (
                                <div className={styles.genre} key={genre.id}><h3>{truncateTitle(genre.name)}</h3> <CiCircleInfo dataname="Icon" /></div>
                            )
                        })}
                        {info.runtime > 0 && (<h3 className={styles.runtime}>{info.runtime}  <CiTimer /></h3>)}
                        {info.vote_average > 0 && (<h3 className={styles.vote}>{info.vote_average.toFixed(1)} <CiHeart /></h3>)}
                    </div>
                </div>
            </div>
        </>
    )
}

export default Movie